










export default {
	name : "graceMaskView",
	props :{
		width:{
			type : String,
			default : "80%"
		},
		show : {
			type : Boolean,
			default : false
		},
		bgcolor : {
			type : String,
			default : "#FFFFFF"
		}
	},
	methods:{
		close : function(){
			this.$emit('close')
		}
	}
}
